header {
  text-decoration: none;
  border-bottom: 1px solid rgb(211, 205, 205);

  display: flex;
  justify-content: space-between;
  padding: 40px;
  .header__info p {
    text-decoration: none;

    opacity: 0.7;
    font-size: 15px;
    font-weight: 800;
    text-transform: uppercase;
  }
  a {
    color: rgb(39, 39, 39);
    text-decoration: none;
  }
  .header__left {
    text-decoration: none;

    display: flex;
    align-items: center;
  }
  img {
    margin-right: 15;
  }
  h3,
  p {
    margin: 0;
  }
  h3 {
    text-transform: uppercase;
    font-weight: 800;
  }
  ul,
  li {
    cursor: pointer;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  .header__right {
    .cart {
      transition: 0.2s ease-in-out;
    }
    .cart:hover {
      font-size: 20px;
    }
    img {
      margin-bottom: -7px;
    }
    li {
      margin-right: 20px;
    }
    display: flex;
    margin-right: 30px;
    align-items: center;
  }
  span {
    font-weight: 800;
  }
}
