$main-shadow: 0px 14px 30px rgba(0, 0, 0, 0.5);
$deftrans: 0.2s ease-in-out;
$card-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
.card {
	margin-top: 10px;
	margin-right: 30px;
	border-radius: 40px;
	padding: 30px;
	width: 210px;
	border: 1px solid #e2e1e1;
	transition: $deftrans, transform 0.2s ease-in-out;
	span {
		font-size: 16px;
		opacity: 0.5;
		text-transform: uppercase;
	}
	b {
		font-size: 18px;
	}
	.favorite {
		position: absolute;
		cursor: pointer;
	}
	.card__info {
		display: flex;
		justify-content: space-between;
	}
	.card__main {
		display: flex;
		flex-direction: column;
	}
	button {
		background-color: white;
		border-radius: 8px;
		box-sizing: border-box;
		border: 1px;
		cursor: pointer;
	}
	.card__plus {
		cursor: pointer;
	}
	box-shadow: $card-shadow;
}
.card:hover {
	box-shadow: 0px 14px 30px rgba(0, 0, 0, 0.2);
	transform: translateY(-5px);
}
